import React,{ Component } from 'react';
import { LivePreview } from './LivePreview';
import { SlideShowButton } from './SlideShowButton';

export class SlideShowApp extends Component {
	constructor(props,context) {
		super(props,context);
		this.state = {
			text:''
		};
		this.handlechange = this.handlechange.bind(this);
	}

	componentWillUnmount() {
	}

	componentDidMount() {
	}

	handlechange(e) {
		this.setState({text:e.target.value});
	}

	render() {
		return (
			React.createElement("div", {className: "row"}, 
				React.createElement("div", {className: "col-md-6"}, 
					React.createElement(SlideShowButton, {className: "btn btn-primary"}), 
					React.createElement("textarea", {className: "form-control", rows: "10", onChange: this.handlechange})
				), 
				React.createElement("div", {className: "col-md-6"}, 
					React.createElement(LivePreview, {text: this.state.text})
				)
			)
		);
	}
}

SlideShowApp.defaultProps = {
	
}